<template>
    <div class="register-wrap">
        <div v-title data-title="完善信息">完善信息</div>
        <header class="logoIn-header">
           <span @click="remove" class="btn-back">ShopsN</span>{{title}}
        </header>
        <div class="from">
            <div class="input-main userName">
                <span class="icon"></span>
                <input type="text" placeholder="请输入用户名" v-model="username">
            </div>
            <div class="input-main passWord">
                <span class="icon"></span>
                <input type="password" placeholder="请输入密码...." v-model="password">
            </div>
            <div class="input-main passWord">
                <span class="icon"></span>
                <input type="password" placeholder="请再次输入密码...." v-model="re_password">
            </div>
            <div class="input-main rec">
                <span class="icon"></span>
                <input type="text" placeholder="选填推荐码" :disabled="status" v-model="p_id">
            </div>
            <button class="btn-in" @click="register">确&nbsp;&nbsp;定</button>
        </div>
    </div>
</template>
<script>
    import { Toast } from 'mint-ui';
    import Qs from 'qs'
    export default {
        name : 'register',
        data(){
            return {
                title:'完善信息',
                username : '',
                password:'',
                re_password:'',
                scrollWatch:true,
                p_id:'',
                status:false
            }
        },
        methods : {
            remove(){
                this.$router.go(-1);
            },
            register(){
                this.axios.post(API_URL + 'Home/Register/register_updata',Qs.stringify({
                    user_name:this.username,
                    password:this.password,
                    re_password:this.re_password,
                    p_id:this.p_id
                })).then((res) => {
                    Toast(res.data.msg);
                    if(res.data.status == 1){
                        sessionStorage.setItem('user_ID', res.data.data);
                        this.$router.push('/Home');
                    }
                }).catch((err) => {
                    console.log(err)
                });
            }
        
        },
        mounted() {
            document.body.scrollTop = 0;
            var pa =window.location.href;
            if(pa.indexOf("=")>-1){
                var tpa = pa.split('=');
                var spa = tpa[1].split('&');
                this.p_id = spa[0];
                this.status = true;
            }
             
        },
        destroyed(){
            this.scrollWatch = false;
        }
    }
</script>
<style lang="less" scoped>
    .register-wrap{
        padding:0 .6rem;
        background:#fff;
        .logoIn-header{
            width:100%;
            height:.9rem;
            line-height:.9rem;
            text-align:center;
            font-size:.36rem;
            color:#d0111b;
            position:relative;
            .btn-back{
                width:1.3rem;
                height:100%;
                font-size:.28rem;
                color:#999;
                position:absolute;
                left:-.6rem;
                top:0;
            }
        }
        .from{
            .input-main{
                height:1.1rem;
                margin-top:.3rem;
                position:relative;
                .icon{
                    width:1.1rem;
                    height:100%;
                    position:absolute;
                    left:0;
                    top:0;
                }
                input{
                    width:100%;
                    height:100%;
                    border:none;
                    background:#f5f5f5;
                    text-indent:1.1rem;
                    border-radius:5px;
                    font-size:.28rem;
                    line-height:100%;
                }
            }
            .userName{
                .icon{
                    background:url(../../assets/userName.png) no-repeat center;
                    background-size:.44rem .55rem;
                }
            }
            .phone-number{
                .icon{
                    background:url(../../assets/phone.png) no-repeat center;
                    background-size:.36rem .5rem;
                }
            }
            .message{
                .icon{
                    background:url(../../assets/message.png) no-repeat center;
                    background-size:.41rem .33rem;
                }
                .btn-ver{
                    width:1.88rem;
                    height:1.04rem;
                    position:absolute;
                    right:.04rem;
                    top:50%;
                    margin-top:-.52rem;
                    border:none;
                    background:#fff;
                    font-size:.26rem;
                    color:#333;
                    border-radius:5px;
                    outline:none;
                }
                .btn-ver:active{
                    background:#f9781e;
                    color:#fff;
                }
                .btn-ver.active{
                    background:#f9781e;
                    color:#fff;
                }
            }
            .passWord{
                .icon{
                    background:url(../../assets/passWord.png) no-repeat center;
                    background-size:.44rem .51rem;
                }
            }
            .rec{
                .icon{
                     background:url(../../assets/rec.png) no-repeat center;
                     background-size:.5rem .46rem;
                }
            }
            .btn-in{
                width:100%;
                height:1.1rem;
                border:none;
                border-radius:20px;
                outline:none;
                margin-top:.3rem;
                background:#d0111b;
                font-size:.36rem;
                color:#fff;
                margin-bottom:.5rem;
            }
            .btn-in:active{
                box-shadow: 0 5px 5px #ccc;
            }
        }
        .return-btn{
            height:.95rem;
            width:100%;
            text-align:center;
            line-height:.95rem;
            .btn{
                display:inline-block;
                font-size:.28rem;
                color:#797979;
            }
        }
    }
</style>


